// src/components/UserPage.jsx

import React, { useState } from 'react';
import Calendar from 'react-calendar';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import CheckoutForm from './CheckoutForm'; // Import the CheckoutForm component
import './UserPage.css';

// Load your Stripe public key here
const stripePromise = loadStripe('YOUR_STRIPE_PUBLIC_KEY');

const UserPage = () => {
    const [date, setDate] = useState(new Date());

    return (
        <div className="user-page">
            <h1>User Dashboard</h1>
            
            <section>
                <h2>Bookings</h2>
                <p>Your booking details will be displayed here.</p>
                {/* Implement bookings functionality */}
            </section>
            
            <section>
                <h2>Images</h2>
                <img src="data:image/jpeg;base64," alt="Sample" className="user-image" />
                {/* Display multiple images or a gallery */}
                <button>Book Now</button>
            </section>
            
            <section>
                <h2>Description</h2>
                <p>Description about the user or the service.</p>
                {/* Add detailed descriptions here */}
            </section>
            
            <section>
                <h2>Availability</h2>
                <Calendar
                    onChange={setDate}
                    value={date}
                />
                <p>Selected date: {date.toDateString()}</p>
            </section>
            
            <section>
                <h2>Payment Options</h2>
                <Elements stripe={stripePromise}>
                    <CheckoutForm />
                </Elements>
            </section>
        </div>
    );
};

export default UserPage;
